.payment {
  background-color: white;
}
.payment-container {
  text-align: center;
  padding: 10px;
  font-weight: 400;
  /* background-color: rgb(234, 237, 237); */
  border-bottom: 1px solid lightgrey;
}
.payment-container > h1 a {
  text-decoration: none;
}
.payment-section {
  display: flex;
  padding: 20px;
  margin: 0 20px;
  border-bottom: 1px solid lightgrey;
}
.payment-title {
  flex: 0.2;
}
.payment-address,
.payment-items {
  flex: 0.8;
}
.payment-details {
  flex: 0.6;
}
